import React from 'react';

import Navigation from './navigation';
import Card from './card';
import basicComponent from './basicComponent';

class Container extends basicComponent {

    className = "container";

    style = {
        navigation: {
            'top': 0,
            'left': 0,
            'height': 0,
            'width': 0,
            'line-height': 0,
        },

        articleCard: {
            'top': 0,
            'left': 0,
            'height': 0,
            'width': 0,
            'line-height': 0,
        }
    }

    constructor(props) {
        super(props);

        // create dom ref
        this.dom = React.createRef();

        // create measure state
        this.state = { height: 0, width: 0 };

    }

    measure() {
        // set state
        var clientHeight = this.dom.current.clientHeight;
        var clientWidth = this.dom.current.clientWidth;

        this.setState({ height: clientHeight, width: clientWidth });

        // alter nav style
        this.style.navigation['height'] = clientHeight * 0.1;
        this.style.navigation['line-height'] = clientHeight * 0.1;
        this.style.navigation['width'] = clientWidth;

        // alter art style
        this.style.articleCard['top'] = clientHeight * 0.1;
        this.style.articleCard['height'] = clientHeight * 0.9;
        this.style.articleCard['line-height'] = clientHeight * 0.9;
        this.style.articleCard['width'] = clientWidth;
    }

    componentDidMount() {
        this.measure();
        console.log(this.style);

        // hook window
        window.addEventListener('resize', () => this.measure());
    }

    componentWillUnmount() {
        // unhook window
        window.removeEventListener('resize', () => this.measure());
    }

    render() {
        return (
            <div id="container" ref={this.dom} className={this.className}>
                <Navigation
                    style={Object.assign({}, this.style.navigation)}
                    items="['Riley\'s Github Page', '主頁', '關於']"
                    links="['#', '#', '#']"
                />
                <Card style={Object.assign({}, this.style.articleCard)} />
            </div>
        );
    }
}

export default Container;