import React from 'react';

import basicComponent from './basicComponent';

class Navigation extends basicComponent {

    className = "navigation";

    constructor(props) {
        super(props);
    }

    list(items, links) {
        return items.map((item, index) =>
            <li key={index}><a href={links[index]}>{item}</a></li>
        );
    }

    render() {
        return (
            <div style={this.getStyle()} className={this.className} ref={this.measure}>
                <ul>{this.list(eval(this.props.items), eval(this.props.links))}</ul>
            </div>
        );
    }
}

export default Navigation;